import React from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import styles from './DateRangePicker.module.css';
interface DateRangePickerProps {
  startDate: Date;
  endDate: Date;
  onStartDateChange: (date: Date) => void;
  onEndDateChange: (date: Date) => void;
}

/*************  ✨ Codeium Command 🌟  *************/
export const DateRangePicker: React.FC<DateRangePickerProps> = React.memo(
  ({ startDate, endDate, onStartDateChange, onEndDateChange }) => {
    const handleChange = (date: Date | null, changed: 'start' | 'end') => {
        if (date) {
        changed === 'start'
            ? onStartDateChange(date)
            : onEndDateChange(date);
        }
      };
    return (
      <div className={styles.dateRangePicker}>
        <DatePicker
          selected={startDate}
          onChange={(date) => date && handleChange(date, 'start')}
          selectsStart
          startDate={startDate}
          endDate={endDate}
          className={styles.datePicker}
        />
        <DatePicker
          selected={endDate}
          onChange={(date) => date && handleChange(date, 'end')}
          selectsEnd
          startDate={startDate}
          endDate={endDate}
          minDate={startDate}
          className={styles.datePicker}
        />
      </div>
    );
  }
);
